/* eslint-disable react/jsx-props-no-spreading */
import React, { useCallback, useState } from 'react';

export default function Card(props) {
    const {
        wrapperClasses,
        contentClasses,
        children,
        id,
        headingIcon,
        isCollapsable,
        isScrollable,
        subTitle,
        title,
        ...rest
    } = props;

    let storageId = ``;

    if (id) {
        storageId = `ComponentCard-${id}`;
    }

    let storageIsCollapsed = false;
    if (storageId) {
        storageIsCollapsed = localStorage.getItem(storageId) === `collapsed`;
    }

    const [isCollapsed, setCollapsed] = useState(storageIsCollapsed);

    const wrapperClass = ['card', wrapperClasses].join(' ');
    const contentClass = [
        'contents',
        contentClasses,
        isScrollable && 'scrollable',
    ].join(' ');
    const collapseClass = [
        'svg-icon',
        isCollapsed === false && 'collapsed',
    ].join(' ');

    const toggleIsCollapsed = useCallback(() => {
        setCollapsed(!isCollapsed);

        if (storageId) {
            localStorage.setItem(storageId, !isCollapsed ? `collapsed` : ``);
        }
    }, [storageId, isCollapsed]);

    return (
        <div className={wrapperClass} id={id} {...rest}>
            <div className="header">
                <h2 className="header-text">{title}</h2>
                <div className="header-right">
                    {subTitle && (
                        <h3 className="header-text sub-title">{subTitle}</h3>
                    )}
                    {isCollapsable && (
                        <button
                            type="button"
                            onClick={toggleIsCollapsed}
                            className="more-menu-button"
                            aria-expanded={!isCollapsed}
                            aria-controls={`content-${id}`}
                        >
                            <svg className={collapseClass}>
                                <title>
                                    {isCollapsed
                                        ? `Expand panel`
                                        : `Collapse panel`}
                                </title>
                                <use href="#chevron" />
                            </svg>
                        </button>
                    )}
                    {headingIcon}
                </div>
            </div>
            {(isCollapsable !== true ||
                (isCollapsable === true && isCollapsed === false)) && (
                <div
                    className={contentClass}
                    id={`content-${id}`}
                    role="region"
                    tabIndex={-1}
                >
                    {children}
                </div>
            )}
        </div>
    );
}
