import { PromiseDebouncer } from '../../libs/PromiseDebouncer';

/* eslint-disable import/prefer-default-export */

export class UserService {
    constructor(dto) {
        if (!dto) {
            throw new Error(
                `dto is required when instantiating a new UserService`
            );
        }

        [`userAdapter`].forEach((name) => {
            if (!dto[name]) {
                throw new Error(
                    `${name} property is required when instantiating a new UserService`
                );
            }
        });

        this.promiseDebouncer = new PromiseDebouncer();
        this.userAdapter = dto.userAdapter;
    }

    /**
     * Get user.
     * @param {string} userId The user id.
     *
     * @returns {Promise<object>} A promised user.
     */
    get(userId) {
        return this.promiseDebouncer.debounce(
            {
                method: `get`,
                userId,
            },
            () => this.userAdapter.get(userId)
        );
    }
}
