import React, { createContext, useEffect, useState } from 'react';

/**
 * The user context provider.
 * @param {object} props The events provider props.
 * @returns {JSX.Element} Event context provider component.
 */
export const UserProvider = ({ children, userService, userId }) => {
    const [isLoading, setIsLoading] = useState(false);
    const [isReady, setIsReady] = useState(false);
    const [user, setUser] = useState([]);

    useEffect(() => {
        setIsLoading(true);
        userService.get(userId).then((response) => {
            setUser(response);
            setIsLoading(false);
            setIsReady(true);
        });
    }, [user]);

    return (
        <UserContext.Provider value={{ user, userService, isLoading, isReady }}>
            {children}
        </UserContext.Provider>
    );
};

/**
 * The context for user.
 * @export
 */
export const UserContext = createContext({
    user: {},
    userId: null,
    userService: null,
    isLoading: false,
    isReady: false,
});
