/* eslint-disable react/jsx-props-no-spreading */
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import Card from '../../edify-card/jsx/Card';
import { UserService } from '../../_data-layer/js/Services/UserService/UserService';
import { UserProvider } from '../../_data-layer/jsx/Contexts/UserContext/UserContext';
import { useUser } from '../../_data-layer/jsx/Hooks/User/useUser';
import CircularProgress from '../../circular-progress/jsx/circular-progress';
import interpolate from '../../_global/js/interpolate';

function Analytics(props) {
    const {
        progressHeading,
        progressAria,
        progressLabel,
        creditsHeading,
        creditsAria,
        creditsLabel,
        scale,
        goodScoreMessage,
        strokeWidth,
        userId,
    } = props;

    const { user } = useUser();
    const [title, setTitle] = useState(`Welcome`);
    const [gpa, setGPA] = useState(0);
    const [studentCredits, setStudentCredits] = useState(0);

    const date = new Date();

    useEffect(() => {
        if (user === null) {
            return;
        }

        const { firstName, credits, GPA } = user;
        const hours = date.getHours();

        setGPA(GPA || 0);
        setStudentCredits(credits || 0);

        let newTitle = ``;

        if (hours < 13) {
            newTitle = `Good morning`;
        } else if (hours < 18) {
            newTitle = `Good afternoon`;
        } else {
            newTitle = `Good evening`;
        }

        if (gpa < 0.7 * scale) {
            newTitle = `Welcome back`;
        } else if (gpa > 0.75 * scale) {
            newTitle = goodScoreMessage;
        }

        if (date.getDay() === 5) {
            newTitle = `Happy Friday`;
        }

        setTitle(`${newTitle}, ${firstName}`);
    }, [gpa, date, user]);

    return (
        <Card
            isCollapsable
            title={title}
            contentClasses="analytics__container"
            id={`card-${userId}`}
        >
            <CircularProgress
                percentage={gpa}
                scale={scale}
                strokeWidth={strokeWidth}
            />
            <div className="analytics__progress-details">
                <h4>{progressHeading}</h4>
                <span className="sr-only">
                    {interpolate(progressAria, { gpa, scale })}
                </span>
                <p aria-hidden>{interpolate(progressLabel, { gpa, scale })}</p>
            </div>
            <CircularProgress
                wrapperClasses="analytics__progress-circle--grey"
                percentage={studentCredits}
                showProgress={false}
            />
            <div className="analytics__progress-details">
                <h4>{creditsHeading}</h4>
                <span className="sr-only">
                    {interpolate(creditsAria, { studentCredits })}
                </span>
                <p aria-hidden>
                    {interpolate(creditsLabel, { studentCredits })}
                </p>
            </div>
        </Card>
    );
}

const { number, string } = PropTypes;

Analytics.propTypes = {
    scale: number,
    progressHeading: string,
    progressAria: string,
    progressLabel: string,
    creditsHeading: string,
    creditsAria: string,
    creditsLabel: string,
    goodScoreMessage: string,
    strokeWidth: number,
};

Analytics.defaultProps = {
    scale: 50,
    progressHeading: 'Total GPA',
    progressAria: 'You have {gpa} of a possible {scale}',
    progressLabel: '{gpa}/{scale}',
    creditsHeading: 'Completed credits',
    creditsAria: 'You have completed {studentCredits} of your credits',
    creditsLabel: '{studentCredits} credits',
    goodScoreMessage: 'Great GPA! keep it up',
    strokeWidth: 2,
};

export default function AnalyticsProvider(props) {
    const { userAdapter, userId } = props;

    const userService = new UserService({ userAdapter });

    return (
        <UserProvider userService={userService} userId={userId}>
            <Analytics {...props} />
        </UserProvider>
    );
}
