import React from 'react';
import PropTypes from 'prop-types';
import interpolate from '../../_global/js/interpolate';

export default function CircularProgress(props) {
    const {
        strokeWidth,
        percentage,
        scale,
        showProgress,
        wrapperClasses,
        descriptionTemplate,
    } = props;

    const wrapperClass = [
        'circular-progress-circle',
        'circular-progress-circle__container',
        wrapperClasses,
    ].join(' ');

    // We have to change the radius of the circle so that the formula (Circumference / scale) * Percentage to fill equals Percentage to fill.
    const radius = scale / (Math.PI * 2);
    const coordinates = radius + strokeWidth;

    const viewBoxSize = coordinates * 2;
    const viewBox = `0 0 ${viewBoxSize} ${viewBoxSize}`;

    return (
        <section className={wrapperClass}>
            <svg
                className="circular-progress-circle__chart"
                viewBox={viewBox}
                style={{
                    '--circle-percentage': percentage,
                    '--circle-stroke': strokeWidth,
                    '--circle-scale': scale,
                }}
            >
                <circle
                    className="circle circular-progress-circle__chart--background"
                    cy={coordinates}
                    cx={coordinates}
                    r={radius}
                />
                {showProgress && (
                    <circle
                        className="circle circular-progress-circle__chart--progress"
                        cy={coordinates}
                        cx={coordinates}
                        r={radius}
                    />
                )}
            </svg>
            <h2 className="circular-progress-circle__description">
                {interpolate(descriptionTemplate, { percentage })}
            </h2>
        </section>
    );
}

const { number, bool, string } = PropTypes;

CircularProgress.propTypes = {
    strokeWidth: number,
    percentage: number,
    scale: number,
    showProgress: bool,
    descriptionTemplate: string,
};

CircularProgress.defaultProps = {
    strokeWidth: 1,
    percentage: 0,
    scale: 100,
    showProgress: true,
    descriptionTemplate: '{percentage}',
};
